	news_or_events_form = (modelFunction) => {

		const news_form = document.getElementById("edit_news_form");
		const news_img = document.getElementById("admin_news_and_events_page_main_section_section1_article_div1_div2_div3_img");

		news_form.onsubmit = (event) =>{
			// preventDefault allows you to choose how to submit form without default actions
			event.preventDefault();

			const news_form_title = document.getElementById("edit_news_form_title");
			const news_form_cat1 = document.getElementById("preset_edit_news_categories_select");
			const news_form_cat2 = document.getElementById("edit_news_form_category");
			const news_form_img = document.getElementById("edit_news_form_image");
			const news_form_text = document.getElementById("edit_news_form_article");

			const result = {

			}

			let old_image = null;

			if(news_img.getAttribute("src")){

				const src = news_img.src; 
				old_image = src.split('/').pop();
			}

			if(news_form_title.value.length > 0){

				result.news_headline = news_form_title.value
			}
			if(news_form_cat1.value.length > 0){

				result.news_category = news_form_cat1.value
				news_form_cat2.value = "";
			}
			if(news_form_cat2.value.length > 0){

				result.news_category = news_form_cat2.value

			}
			if(news_form_img.files[0]){

				result.image = news_form_img.files[0]
			}
			if(news_form_text.value.length > 0){

				result.news_article = news_form_text.value
			}

			modelFunction(result,"news",old_image)
		}
	}

	ul_titles_news_li = (modelFunction) => {

		const ul_news_titles = document.getElementById("admin_news_and_events_page_main_section_section1_article_div1_ul2");

		ul_news_titles.addEventListener("click", (event)=>{

			modelFunction(event.target.closest("li").getAttribute("name"))
		})
	}

